import { Chart, ChartConfiguration, ChartData, CoreChartOptions, registerables } from "chart.js";

Chart.register(...registerables);

const DATA_COUNT = 7;
const NUMBER_CFG = { count: DATA_COUNT, min: -100, max: 100 };

const canvas = document.getElementById("myChart") as HTMLCanvasElement;
const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;

const data: ChartData<"bar", number[], string> = {
  labels: ["", "", "", "", "", "", ""],
  datasets: [
    {
      label: "", 
      data: [10, 19, 13, 5, 12, 3],
      backgroundColor: ["rgba(0, 0, 0, 1)"],
      borderWidth: 1, 
      barPercentage: 0.8, 
      categoryPercentage: 0.8
    },
    {
      label: "", 
      data: [5, 2, 3, 12, 19, 3],
      backgroundColor: ["rgba(255, 255, 0, 1)"],
      borderWidth: 1,
      barPercentage: 0.8, 
      categoryPercentage: 0.8
    },
  ]
};

const config: ChartConfiguration<"bar", number[], string> = {
  type: "bar",
  data: data,
  options: {
    responsive: true,
    aspectRatio: 2,
    plugins: {
      tooltip: {
        titleFont: {
          size: 24
        }
      },
      legend: {
        position: "top",
        labels: {
          font: {
              size: 24
          }
      }
      },
      title: {
        display: true,
        text: "",
      },
    },
    scales: {
      x: {
        ticks: {
          font: {
            size: 20
          }
        }
      },
      y: {
        ticks: {
          font: {
            size: 24
          }
        }
      }
    }
  },
};

function avgData(data : number[]) {
  return data.reduce((a, b) => a+b) / data.length;
}

const horizonalLinePlugin = {
  id: "horizonalLine",
  afterDraw: (chart: Chart) => {
    const ctx = chart.ctx;
    ctx.save();
    const xAxis = chart.scales['x'];
    const yAxis = chart.scales['y'];
    const y = yAxis.getPixelForValue(avgData(chart.data.datasets[0].data as number[]));
    ctx.beginPath();
    ctx.moveTo(xAxis.left, y);
    ctx.lineTo(xAxis.right, y);
    ctx.lineWidth = 2;
    ctx.strokeStyle = 'orange';
    ctx.stroke();
    ctx.restore();
  }
}

Chart.register(horizonalLinePlugin);
const myChart = new Chart(ctx, config);


